import { ComponentMeta, ComponentStory } from '@storybook/react';

import { Search } from '../components/Search';

export default {
  title: 'Search',
  component: Search,
  parameters: { actions: { argTypesRegex: '^on.*' } },
} as ComponentMeta<typeof Search>;

const getHints = async (query: string) =>
  new Promise<string[]>((resolve) => {
    setTimeout(() => {
      resolve([`${query} suggestion 1`, `${query} suggestion 2`, `${query} suggestion 3`]);
    }, 300);
  });
const Template: ComponentStory<typeof Search> = (args) => <Search {...args} getHints={getHints} />;

export const SearchExample = Template.bind({});
SearchExample.args = { buttonFill: 'solid', buttonVariant: 'primary' };



SearchExample.parameters = { storySource: { source: "(args) => <Search {...args} getHints={getHints} />" }, ...SearchExample.parameters };