/** @jsxImportSource @emotion/react */
import { css } from '@emotion/react';
import { Button } from './Button';
import { FillOptions, Variants } from './types';
import React from 'react';
import { palette } from './palette';
import { debounce } from 'lodash';
import { useClickAway } from './useClickAway';

export type SearchProps = {
  onSearch: (query: string) => void;
  getHints: (query: string) => Promise<string[]>;
  buttonVariant?: Variants;
  buttonFill?: FillOptions;
  debounceTimeMs?: number;
};

export function Search({ onSearch, getHints, buttonVariant, buttonFill, debounceTimeMs }: SearchProps) {
  const [query, setQuery] = React.useState('');
  const [hints, setHints] = React.useState<string[]>([]);
  const [showHints, setShowHints] = React.useState(false);

  const handleSearch = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    onSearch(query);
  };

  const handleHintClick = (hint: string) => {
    setQuery(hint);
    setShowHints(false);
  };

  const processHints = React.useMemo(() => {
    const func = async (newQuery: string) => {
      const newHints = await getHints(newQuery);
      setHints(newHints);
    };
    return debounce(func, debounceTimeMs);
  }, [debounceTimeMs, getHints]);

  const handleQueryChange = async (event: React.ChangeEvent<HTMLInputElement>) => {
    const newQuery = event.target.value;
    setQuery(newQuery);

    processHints(newQuery);
  };

  const ref = React.useRef<HTMLFormElement | null>(null);
  useClickAway(ref, () => {
    setShowHints(false);
  });

  return (
    <form ref={ref} css={styles.form} onSubmit={handleSearch}>
      <input css={styles.input} value={query} onChange={handleQueryChange} onFocus={() => setShowHints(true)} />
      {showHints && hints.length > 0 && (
        <ul css={styles.hintsContainer}>
          {hints.map((hint, index) => (
            <li key={index} onClick={() => handleHintClick(hint)}>
              {hint}
            </li>
          ))}
        </ul>
      )}
      <Button css={styles.button} type="submit" variant={buttonVariant} fill={buttonFill}>
        Search
      </Button>
    </form>
  );
}

const styles = {
  form: css`
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    display: flex;
    align-items: center;
    width: 100%;
    max-width: 400px;
    margin: 0 auto;
    position: relative;
  `,
  input: css`
    padding: 8px;
    border: none;
    font-size: 16px;
    border-radius: 4px 0 0 4px;
    width: 100%;
    &:focus {
      outline: none;
      box-shadow: inset 0 0 2px 2px ${palette.primary};
    }
  `,
  button: css`
    padding: 8px 16px;
    border-radius: 0 4px 4px 0;
    font-size: 16px;
  `,
  hintsContainer: css`
    position: absolute;
    left: 0;
    right: 0;
    list-style: none;
    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 4px;
    margin: 0;
    padding: 0;
    top: 100%;

    li {
      padding: 8px;
      cursor: pointer;
      &:hover {
        background-color: #ddd;
      }
    }
  `,
};
