import { ComponentMeta, ComponentStory } from '@storybook/react';

import { Tree } from '../components/Tree';

export default {
  title: 'Tree',
  component: Tree,
  parameters: { actions: { argTypesRegex: '^on.*' } },
} as ComponentMeta<typeof Tree>;

const Template: ComponentStory<typeof Tree> = (args) => <Tree {...args} />;

let idCounter = 0;
function nextId() {
  return idCounter++;
}

export const TreeExample = Template.bind({});
TreeExample.args = {
  data: [
    {
      id: nextId(),
      label: 'Node 1',
      children: [
        {
          id: nextId(),
          label: 'Child 1 of Node 1',
        },
        {
          id: nextId(),
          label: 'Child 2 of Node 1',
        },
      ],
    },
    {
      id: nextId(),
      label: 'Node 2',
      children: [
        {
          id: nextId(),
          label: 'Child 1 of Node 2',
        },
        {
          id: nextId(),
          label: 'Child 2 of Node 2',
          children: [
            {
              id: nextId(),
              label: 'Grandchild 1 of Node 2, Child 2',
            },
          ],
        },
      ],
    },
    {
      id: nextId(),
      label: 'Node 3',
      children: [
        {
          id: nextId(),
          label: 'Child 1 of Node 3',
        },
      ],
    },
  ],
};



