/** @jsxImportSource @emotion/react */
import { css, keyframes } from '@emotion/react';
import React from 'react';
import { Button } from './Button';

type CarouselProps = React.HTMLAttributes<HTMLDivElement> & {
  images: string[];
  animationTimeMs?: number;
  prev?: React.ReactNode;
  next?: React.ReactNode;
};

type AnimationState = {
  direction?: 'left' | 'right';
  state?: 'out' | 'in';
};

export function Carousel({ images, animationTimeMs, prev = 'Prev', next = 'Next', ...htmlDivProps }: CarouselProps) {
  const [currentImageIndex, setCurrentImageIndex] = React.useState(0);

  const [nextImageIndex, setNextImageIndex] = React.useState<number | null>(null);
  const [animationState, setAnimationState] = React.useState<AnimationState | null>(null);

  const handlePrevImage = () => {
    setNextImageIndex((currentImageIndex - 1 + images.length) % images.length);
  };

  const handleNextImage = () => {
    setNextImageIndex((currentImageIndex + 1) % images.length);
  };

  React.useEffect(() => {
    const state = animationState?.state;
    let timeout: NodeJS.Timeout | null = null;
    if (state == null) {
      if (nextImageIndex != null && nextImageIndex !== currentImageIndex) {
        setAnimationState({
          state: 'out',
          direction: shortestDirection(images.length, currentImageIndex, nextImageIndex),
        });
      }
    } else if (state === 'out') {
      timeout = setTimeout(() => {
        if (nextImageIndex != null) {
          setCurrentImageIndex(nextImageIndex);
          setAnimationState((prev) => ({
            state: 'in',
            direction: prev?.direction,
          }));
        }
      }, animationTimeMs);
    } else if (state === 'in') {
      timeout = setTimeout(() => {
        setAnimationState(null);
        setNextImageIndex(null);
      }, animationTimeMs);
    }

    return () => {
      if (timeout) {
        clearTimeout(timeout);
      }
    };
  }, [animationState?.state, animationTimeMs, currentImageIndex, images.length, nextImageIndex]);

  const animationStyle = React.useMemo(() => {
    if (animationState?.state == null || animationState.direction == null) {
      return null;
    }
    return css`
      animation-duration: ${animationTimeMs}ms;
      animation-name: ${slide[animationState.state][animationState.direction]};
    `;
  }, [animationState, animationTimeMs]);

  return (
    <div {...htmlDivProps} css={styles.container}>
      {images[currentImageIndex] != null ? (
        <div css={styles.carouselContainer}>
          <img
            css={css`
              ${styles.image}
              ${animationStyle}
            `}
            src={images[currentImageIndex]}
            alt="Carousel Item"
          />
        </div>
      ) : (
        <div css={styles.noImage}>No image</div>
      )}
      {images.length > 1 && (
        <>
          <Button css={styles.leftButton} onClick={handlePrevImage}>
            {prev}
          </Button>
          <Button css={styles.rightButton} onClick={handleNextImage}>
            {next}
          </Button>
        </>
      )}
    </div>
  );
}

const carouselButton = css`
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
`;

const styles = {
  container: css`
    position: relative;
  `,
  carouselContainer: css`
    height: 100%;
    overflow: hidden;
  `,
  leftButton: css`
    ${carouselButton};
    left: 5px;
  `,
  rightButton: css`
    ${carouselButton};
    right: 5px;
  `,
  image: css`
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: relative;
    animation-fill-mode: forwards;
  `,
  noImage: css`
    display: flex;
    justify-content: center;
    height: 100%;
    flex-direction: column;
    text-align: center;
  `,
};

const slide = {
  out: {
    right: slideAnimation(0, -100),
    left: slideAnimation(0, 100),
  },
  in: {
    right: slideAnimation(100, 0),
    left: slideAnimation(-100, 0),
  },
};

function slideAnimation(from: number, to: number) {
  return keyframes`
    from {
      left: ${from}%;
    }
    to {
      left: ${to}%;
    }
  `;
}

function shortestDirection(arrayLength: number, A: number, B: number) {
  const distanceRight = (B - A + arrayLength) % arrayLength;
  const distanceLeft = (A - B + arrayLength) % arrayLength;

  if (distanceRight < distanceLeft) {
    return 'right';
  }
  if (distanceLeft < distanceRight) {
    return 'left';
  }
  return 'left';
}

/**
 * [0, 1, 2, 3, 4] [0...
 * length = 5
 *
 * shortestDirection(5, 1, 2) -> 'right'
 * shortestDirection(5, 4, 0) -> 'right'
 * shortestDirection(5, 0, 4) -> 'left'
 * shortestDirection(5, 2, 1) -> 'left'
 * shortestDirection(5, 1, 4) -> 'right'
 * shortestDirection(5, 3, 0) -> 'left'
 */
