import { css } from '@emotion/react';
import { ComponentMeta, ComponentStory } from '@storybook/react';

import { Carousel } from '../components/Carousel';

import examplePhoto from './images/examplePhoto.jpg';
import examplePhoto2 from './images/examplePhoto2.jpg';
import examplePhoto3 from './images/examplePhoto3.jpg';

export default {
  title: 'Carousel',
  component: Carousel,
} as ComponentMeta<typeof Carousel>;

const containerStyle = css`
  width: 500px;
  height: 300px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 15px;
  overflow: hidden;
`;

const Template: ComponentStory<typeof Carousel> = (args) => <Carousel {...args} css={containerStyle} />;

export const CarouselExample = Template.bind({});;
CarouselExample.args = {
  images: [examplePhoto, examplePhoto2, examplePhoto3],
  animationTimeMs: 300,
};

export const NoImages = Template.bind({});;
NoImages.args = {
  images: [],
  animationTimeMs: 300,
  next: 'Next',
  prev: 'Prev',
};

export const SingleImage = Template.bind({});
SingleImage.args = {
  images: [examplePhoto],
  animationTimeMs: 300,
  next: 'Next',
  prev: 'Prev',
};



CarouselExample.parameters = { storySource: { source: "(args) => <Carousel {...args} css={containerStyle} />" }, ...CarouselExample.parameters };
